
.el-vue-search-box-container {
  position: relative;
  width: 360px;
  height: 45px;
  background: #fff;
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.15);
  border-radius: 2px 3px 3px 2px;
  z-index: 10;
}
.el-vue-search-box-container .search-box-wrapper {
  position: absolute;
  display: flex;
  align-items: center;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
}
.el-vue-search-box-container .search-box-wrapper input {
  flex: 1;
  height: 20px;
  line-height: 20px;
  letter-spacing: .5px;
  font-size: 14px;
  text-indent: 10px;
  box-sizing: border-box;
  border: none;
  outline: none;
}
.el-vue-search-box-container .search-box-wrapper .search-btn {
  width: 45px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: transparent;
  cursor: pointer;
}
.el-vue-search-box-container .search-tips {
  position: absolute;
  top: 100%;
  border: 1px solid #dbdbdb;
  background: #FFF;
  overflow: auto;
}
.el-vue-search-box-container .search-tips ul {
  padding: 0;
  margin: 0;
}
.el-vue-search-box-container .search-tips ul li {
  height: 40px;
  line-height: 40px;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
  padding: 0 10px;
  cursor: pointer;
}
.el-vue-search-box-container .search-tips ul li.autocomplete-selected {
  background: #eee;
}



